<template>
  <flexBox>
    <div class="title-1">
      
    </div>
    <titleSub>
      
    </titleSub>
    <text-sub>
      https://github.com/Slothingg/Xgun-components/vue-components/bullet.vue
    </text-sub>
    <titleSub>
      
    </titleSub>
    <text-sub>
      vue2.x(PS:vue3)
    </text-sub>
    <titleSub>
      
    </titleSub>
    <bullet class="bullet col" :item="data"></bullet>
    <Input v-model="modelValue" @on-enter="submit" placeholder="">
    <button slot="suffix" class="btn" @click="submit"></button>
    </Input>
    <codemirror class="codemirror col" :value="code" :options="cmOptions"> </codemirror>
    <div class="title-1 col">
      Api
    </div>
    <apiTable :data="apis"></apiTable>
    <div class="title-1 col">
      Events
    </div>
    <eventTable :data="events"></eventTable>
  </flexBox>
</template>

<script>
  import 'codemirror/mode/vue/vue.js'
  import bullet from '@/components/bullet.vue'
  export default {
    data() {
      return {
        data: [],
        modelValue: '',
        code: `
        <bullet :items="data"></bullet>

        data: []
        `,
        cmOptions: {
          mode: "text/javascript",
          theme: "base16-dark",
          height: 'auto'
        },
        apis: [{
            porps: 'item',
            instruction: ',text(data:[{...,text:"abc"}])',
            type: 'Array',
            defalut: '{}'
          },
          {
            porps: 'canvasWidth',
            instruction: '(px)',
            type: 'Number',
            defalut: '750'
          },
          {
            porps: 'canvasHeight',
            instruction: '(px)',
            type: 'Number',
            defalut: '300'
          },
          {
            porps: 'duration',
            instruction: '()',
            type: 'Number',
            defalut: '5'
          },
          {
            porps: 'zIndex',
            instruction: '',
            type: 'Number',
            defalut: '10'
          },
          {
            porps: 'fontSize',
            instruction: '(px)',
            type: 'Number',
            defalut: '15'
          }
        ],
        events: [{
            events: 'clickItem',
            instruction: '',
            ret: 'item'
          },
          {
            events: 'doubleClick',
            instruction: '',
            ret: ''
          }
        ]
      }
    },
    components: {
      bullet
    },
    methods: {
      submit() {
        this.data.push({
          text: this.modelValue
        })
        this.modelValue = ''
      }
    }
  }
</script>

<style scoped>
  .col {
    margin-top: 10px;
  }

  .btn {
    font-size: 5px;
    width: 100%;
    height: 100%;
    background-color: #2d8cf0;
    border: none;
    color: white;
    transition: all 0.2s;
  }

  .btn:hover {
    box-shadow: 0px 0px 1px 1px #2d8cf0;
    text-shadow: 0px 0px 1px white;
  }

  .bullet {
    border: solid 1px;
    margin-bottom: 2px;
  }

  >>>.CodeMirror {
    height: 150px !important;
  }
</style>



